:root{
    --primary: #black;
}

.btn{
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn:hover{
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #6568f4;
    transition: 250ms;
}

.btn--primary{
    background-color: var(--primary);
    backdrop-filter: blur(10px);
}

.btn--outline{
    background-color: transparent;
    color: #fff;
    padding: 8px 20px;
    border-radius: 1px solid var(--primary);
    transition: all;
}

.btn--medium{
    padding: 8px 20px;
    border-radius: 10px;
    font-size: 18px;
    color: #fff; 
}

.btn--large{
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
}

